@import "src/common/styles/sass/variables";

.sideColumn {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background-color: $primaryColor;
  position: relative;
  min-height: 100vh;
  padding-right: 15px;
  padding-left: 15px;
  @media screen and (min-width: 992px) {
    width: 16.66666667%;
    float: left;
    h2 {
      font-size: 50px;
      margin-top: 20px;
      margin-bottom: 10px;
      font-family: inherit;
      font-weight: 500;
      line-height: 1.1;
      color: inherit;
      display: block;
      margin-block-start: 0.83em;
      margin-block-end: 0.83em;
      margin-inline-start: 0;
      margin-inline-end: 0;

      .baffleText {
        display: block;
        transform: rotate(-90deg);
        text-align: center;

        .baffleSpan {
          color: #fff;
          font-family: $secondaryFont;
          font-size: 60px;
          margin: 10px 0 20px;
          line-height: 60px;
          font-weight: 700;
          text-transform: uppercase;
          letter-spacing: 10px;
        }
      }
    }
  }
  @media screen and (max-width: 991px) {
    height: 100px;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-color: $primaryColor;
    position: relative;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
  };

  }